
* {
    margin: 0;
    padding: 0;
}
#magnifier, #mark, #floatBox, #smallImg, #smallBox, #bigBox, #bigImg {
    position: absolute;
    z-index: 10000;
}
#smallBox, #mark {
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 10005;
}
#floatBox {
    background-color: #fff;
    opacity: 0.5;
    z-index: 10003;
}
#smallImg {
    left: 0;
    top: 0;
    z-index: 10001;
}
#bigBox {
    top: 0px;
    overflow: hidden;
}
.magnifier-transition {
    border: 1px solid #DEDEDE;
    border-radius: 2px;
    transition: all .5s ease;
}
.magnifier-enter, .magnifier-leave {
    opacity: 0;
    height: 0;
}



